<template>
    <v-layout row wrap>
      <v-flex xs12 class="ma-3">
        <v-alert
          v-model="alert"
          success
          transition="scale-transition"
          class="mb-3"
        >
        {{ alertText }}
        </v-alert>
        <v-card>
          <v-card-title class="orange darken-1 white--text" primary-title>
            <div class="headline"> Register for the {{ currentYear }} Alumni Game</div>
          </v-card-title>
          <v-card-text>
          <div id="my-form">
            <v-form v-model="valid" class="pa-4">
              <v-text-field
                label="Name"
                v-model="name"
                :rules="nameRules"
                :counter="10"
                required
              ></v-text-field>
              <v-text-field
                label="E-mail"
                v-model="email"
                :rules="emailRules"
                required
              ></v-text-field>
              <v-select
                label="Select Year"
                v-model="yearResult"
                :items="year"
                required
              ></v-select>
              <v-select
                label="Select Position"
                v-model="positionResult"
                :items="position"
                required
              ></v-select>
              <v-btn @click="register" :class="{ green: valid, red: !valid }">submit</v-btn>
            </v-form>
          </div>
        </v-card-text>
      </v-card>
      </v-flex>
    </v-layout>
</template>

<script>
  export default {
    data () {
      return {
        valid: false,
        name: '',
        alert: false,
        alertText: 'You were successfully registered for the 2017 Alumni Game!',
        yearResult: null,
        positionResult: null,
        currentYear: 2017,
        nameRules: [
          (v) => !!v || 'Name is required',
          (v) => v.length <= 25 || 'Name must be less than 10 characters'
        ],
        email: '',
        emailRules: [
          (v) => !!v || 'E-mail is required'
          // (v) => /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(v) || 'E-mail must be valid'
        ],
        year: [
          'Freshman',
          'Sophomore',
          'Junior',
          'Senior',
          'Alumnus'
        ],
        position: [
          'Attack',
          'Midfield',
          'Defense',
          'Goalie',
          'Faceoff'
        ]

      }
    },
    methods: {
      register () {
        // This is where we construct the JSON object for information transport
        let playerJSON = {
          name: this.name,
          email: this.email,
          year: this.yearResult,
          position: this.positionResult
        }
        // We would want to send playerJSON server side, but for now we will just log it
        // Axios POST api/player/{id}/register
        console.log(playerJSON)
        // Later on, we will display an alert based on what the response from the server says.
        // For now, we just send a success alert since we aren't contacting the server
        this.alert = true
      }
    }
  }
</script>

<style>
</style>



