<template>
  <div>
  <v-app toolbar>
    <v-navigation-drawer persistent clipped enable-resize-watcher v-model="drawer">
      <v-list class="pt-2" dense>
        <v-list-tile v-for="item in items" :key="item.title" :to="item.url">
          <v-list-tile-action>
            <v-icon>{{ item.icon }}</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title>{{ item.title }}</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      </v-list>
    </v-navigation-drawer>
    <v-toolbar fixed class="indigo accent-4" dark>
      <v-toolbar-side-icon @click.stop="drawer = !drawer"></v-toolbar-side-icon>
      <v-toolbar-title>Marvin Ridge Alumni Game</v-toolbar-title>
      <v-spacer></v-spacer>
      <div class="text-xs-center" id="prof-icon">
        <v-container>
          <v-layout row wrap>
            <!-- I want my menu items to display in line, not over under! -->
            <v-menu
              offset-x
              :close-oncontent-click="true"
              :allow-overflow="true"
              :nudge-width="100"
              v-model="menu"
            >
              <v-btn icon slot="activator">
                <v-icon dark>account_circle</v-icon>
                <v-icon dark>arrow_drop_down</v-icon>
              </v-btn>
              <v-list dense>
                <div v-if="isAuth" id="menu-content">
                    <v-layout column wrap>
                      <v-list-tile v-for="option in options" :key="option.title" :to="option.url">
                        <v-list-tile-content>
                          <v-flex xs12>
                            <v-icon>{{ option.icon }}</v-icon>
                          </v-flex>
                          <v-flex xs12>
                          <v-list-tile-title>{{ option.title }}</v-list-tile-title>
                          </v-flex>
                        </v-list-tile-content>
                      </v-list-tile>
                    </v-layout>
                </div>
                <div v-else id="no-auth-menu-content">
                  <v-layout row wrap>
                    <v-list-tile v-for="option in noAuthOptions" :key="option.title" :to="option.url">
                      <v-list-tile-content>
                        <v-flex xs12>
                          <v-icon>{{ option.icon }}</v-icon>
                        </v-flex>
                        <v-flex xs12>
                        <v-list-tile-title>{{ option.title }}</v-list-tile-title>
                        </v-flex>
                      </v-list-tile-content>
                    </v-list-tile>
                  </v-layout>
                </div>
              </v-list>
            </v-menu>
          </v-layout>
        </v-container>
      </div>
    </v-toolbar>
    <main>
      <div id="main-content">
        <router-view></router-view>
      </div>
    </main>
  </v-app>
  <v-footer class="blue darken-2">
    <v-layout row wrap align-center>
      <v-flex xs12>
        <div class="white--text ml-3">
          Made by
          <a class="white--text" href="mailto:jason.moore2@furman.edu">Jason Moore</a>
          with
          <a class="white--text" href="https://github.com/vuetifyjs/parallax-starter/tree/master/template" target="_blank">Vuetify Parallax Theme</a>
        </div>
      </v-flex>
    </v-layout>
  </v-footer>
  </div>
</template>

<script>
import Home from './Home'
import Roster from './Roster'
import Alumni from './Alumni'
import Profile from './Profile'
import Register from './Register'
export default {
  name: 'main-page',
  components: {
    Home,
    Roster,
    Alumni,
    Profile,
    Register
  },
  data () {
    return {
      isAuth: true,
      drawer: true,
      menu: false,
      items: [
        { title: 'Home', icon: 'home', name: 'home', url: '/' },
        { title: 'Alumni Game Registration', icon: 'content_paste', name: 'register', url: '/register' },
        { title: 'About the Team', icon: 'info', name: 'roster', url: '/roster' },
        { title: 'About the Alumni', icon: 'info_outline', name: 'alumni', url: '/alumni' },
        { title: 'GPA Calculator', icon: 'view_module', name: 'gpa', url: '/gpa' },
        { title: 'Kaleidoscope', icon: 'gesture', name: 'kaleidoscope', url: '/kaleidoscope' }
      ],
      options: [
        { title: 'My Profile', icon: 'face', url: '/profile' },
        { title: 'Sign Out', icon: 'exit_to_app', url: '/' }
      ],
      noAuthOptions: [
        { title: 'Register', icon: 'face', url: '/register' },
        { title: 'Sign In', icon: 'exit_to_app', url: '/signin' }
      ],
      right: null
    }
  }
}
</script>

<style>

</style>



